import { Box, Button, Tooltip } from "@mui/material";
import { FC } from "react";
import { Pixel } from "../ImgCanvas/ImgCanvas";
import { getBackgroundStyleByPixel } from "../../utils/getBackgroundStyleByPixel";
import { convertRgbToLab, convertRgbToXyz } from "./convertation";
import { getAdequateContrast } from "./isAdequateContrast";

export interface PixelInfo {
  rgb: Pixel;
  coordinates: {
    x: number;
    y: number;
  };
}

interface Props {
  onClose: () => void;
  primary: PixelInfo | null;
  secondary: PixelInfo | null;
}

export const ColorsInfo: FC<Props> = ({ onClose, primary, secondary }) => {
  return (
    <div
      style={{
        width: "500px",
        display: "flex",
        flexDirection: "column",
        gap: "8px",
        zIndex: 1,
        background: "white",
        position: "absolute",
        top: 30,
        border: "1px solid black",
        padding: "5px",
      }}
    >
      <Button onClick={onClose} size={"small"}>
        close
      </Button>

      <div style={{ display: "flex", gap: "10px" }}>
        {primary && <ColorInfo pixelInfo={primary} name="primary" />}
        {secondary && <ColorInfo pixelInfo={secondary} name={"second"} />}
      </div>
      {primary && secondary && (
        <div>
          {getAdequateContrast(primary.rgb, secondary.rgb) > 4.5
            ? " "
            : "  "}
          , :{" "}
          {getAdequateContrast(primary.rgb, secondary.rgb).toFixed(2)} <br />
               {">"} 4.5
        </div>
      )}
    </div>
  );
};

function ColorInfo({
  pixelInfo,
  name,
}: {
  pixelInfo: PixelInfo;
  name: string;
}) {
  const { coordinates, rgb } = pixelInfo;
  const { r, g, b } = rgb;
  const { x: xCoord, y: yCoord } = coordinates;

  const { x, y, z } = convertRgbToXyz(rgb);
  const { l, a, b: _b } = convertRgbToLab(rgb);

  return (
    <div style={{ flex: "1" }}>
      {pixelInfo && (
        <Box
          display={"flex"}
          flexDirection={"column"}
          gap={1}
          alignItems={"center"}
        >
          <div>{name}</div>
          <div
            style={{
              width: "20px",
              height: "20px",
              background: getBackgroundStyleByPixel(rgb),
              border: "1px solid black",
            }}
          ></div>
          <div>x: {Math.round(xCoord)}</div>
          <div>y: {Math.round(yCoord)}</div>
          <div>
            rgb
            <Tooltip
              title={
                "RGB (, , ) -   ,         : ,   .       0  255.        ."
              }
            >
              <span>?</span>
            </Tooltip>
            : {r}, {g}, {b}
          </div>
          <div>
            xyz
            <Tooltip
              title={
                "XYZ -   ,  CIE  1931 .  X, Y  Z      ,      .  Y   (),     X  Z   .      ,     ."
              }
            >
              <span>?</span>
            </Tooltip>
            : {x.toFixed(2)}, {y.toFixed(2)}, {z.toFixed(2)}
          </div>
          <div>
            lab
            <Tooltip
              title={
                "Lab -  ,    ,  L  ,  a  b -  ,  a      ,  b    . : L  0  100, a  b     -128  128.   ,     ."
              }
            >
              <span>?</span>
            </Tooltip>
            : {l.toFixed(2)}, {a.toFixed(2)}, {_b.toFixed(2)}
          </div>
        </Box>
      )}
    </div>
  );
}
